
body {
  color: #2c2c2c;
  font-size: 14px;
  font-family: "Montserrat", "Helvetica Neue", Arial, sans-serif;
}

.admin {
  background-color: gray;
  display: -webkit-flex;
  display: flex;
  min-height: 100vh;
  padding: 0;
  color: #FFFFFF;
  position: relative;
}
  
.page-header {
  background-image: url(/static/media/background2.10dc1c7f.jpg);
  background-size: cover;
  background-position: center center;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  min-height: 100vh;
  max-height: 100%;
  padding: 0;
  color: #FFFFFF;
  position: relative;
}

.page-header .title {
  margin-bottom: 15px; 
}

.description {
  color: rgba(255, 255, 255, 0.8);
}

.btn-round {
  border-width: 1px !important;
  border-radius: 30px !important;
  padding-right: 23px !important;
  padding-left: 23px !important;
}

#linkB {
color: white;
text-decoration: underline;
}

.card {
  background-color: rgba(229, 228, 255, 0.4)!important;
}

.md-form{
  position: relative;
    margin-top: 1.5rem;
    margin-bottom: 1.5rem;
}
.form-control {
  margin-left: 30px !important;
  width: 90%!important;
  background-color: transparent !important;
  color: white!important;
  border: none!important;
  border-bottom: 1px solid #ced4da !important;
}

::-webkit-input-placeholder { 
  color: black !important;
  opacity: 1; 
}

::-ms-input-placeholder { 
  color: black !important;
  opacity: 1; 
}

::placeholder { 
  color: black !important;
  opacity: 1; 
}


.prefix {
  float: left;
  top: .25rem;
  font-size: 1.75rem !important;
}

.footer {
  position: fixed;
  width: 100%;
  bottom: 0;
  color: #fff;
}

.footer .copyright {
  font-size: 0.8571em;
  line-height: 1.8; 
}

.vacation {
  display: inline-block;
  float: left;
  width: 200px !important;
  margin: 10px 10px 5px 5px;
}
.card img {
  height: 250px;
  width: auto;
  
}
.price{
  font-size: 26px;
  font-weight: 700;
  margin-top: 55px;
}
.top-right {
  float: right!important;
}

@media screen and (max-width: 420px) {
  .vacation{
    width: 95%!important;
  }
}
